.nav {
    font-family: 'Recursive', sans-serif;
    display: flex;
    .menuIcon {
        font-size: 2rem;
        cursor: pointer;
        opacity: 100%;
        float:right;
        transition: all 0.5s ease;
    }
    .menuIconDiv{
        padding-top:0.5rem;
        position: fixed;
        width: 100%;
    }
    .menuWrap{
        position: fixed;
        background-color: #050505;
        color:#FFFFFF;
        transform: translateX(100vw);
        transition: all 0.5s ease;
        display: flex;
        width: 100vw;
        height: 100vh;
        opacity: 0%;
    }
    .menu{
        display:flex;
        flex-direction: column;
        font-size: 0.7rem;
        padding-left:0.6rem;
        padding-top:0.5rem;
        h1 {
            font-weight: normal;
            padding-top: 0.5rem;
        }
    }
    .active-menu {
        transform: translateX(0%);
        opacity: 100%;
    }
    .active-Icon{
        z-index: inherit;
        color: white;
    }
}